.banner {
  color: white;
  object-fit: contain;
  height: 448px;
  position: relative;
}

.banner_content {
  margin-left: 30px;
  height: 190px;
  padding-top: 140px;
}

.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.desc {
  width: 25%;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  height: 80px;
}

.banner_button {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
}

.banner_button:hover {
  color: #000;
  background: #e6e6e6;
  transition: all 0.2s;
}

.banner_fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  position: absolute;
  content: "";
  width: 100%;
  bottom: 0px;
}

@media (max-width: 600px) {
  .desc {
    width: 80%;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    height: 80px;
  }
}
